<template>
	<div class="mine-data">
		<input type="file" id="file" class="hide" accept="image/*">
		<ul class="mine-data-list">
			<li class="other-data-title">基本资料</li>
			<li class="mine-data-items" @click="changeHeader">
				<div class="items-title">头像</div>
				<div class="items-value">
					<div class="icon"><img src="../assets/ico_header.png"></div>
				</div>
			</li>
			<li class="mine-data-items">
				<div class="items-title">昵称</div>
				<div class="items-value" id="mine-nickname">他与柠檬</div>
			</li>
			<router-link tag="li" to="/SettingSign" class="mine-data-items">
				<div class="items-title">签名</div>
				<div class="items-value" id="mine-sign">未设置</div>
			</router-link>
			<li class="mine-data-items">
				<div class="items-title">性别</div>
				<div class="items-value" id="mine-six">
					<select class="mine-six" dir="rtl">
						<option value="0" selected="selected" class="hide">未设置</option>
						<option value="1">男</option>
						<option value="2">女</option>
					</select>
				</div>
			</li>
		</ul>
		<ul class="other-data-list">
			<li class="other-data-title">账号安全</li>
			<li class="mine-data-items">
				<div class="items-title">手机号</div>
				<div class="items-value" id="mine-phone">188****8888</div>
			</li>
			<router-link tag="li" to="/MineChangepsw" class="mine-data-items">
				<div class="items-title">修改密码</div>
				<div class="items-value"></div>
			</router-link>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'MineData',
	data () {
		return {
			msg:"我的资料"
		}
	},
	created (){
		document.title="修改个人信息";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {
			$("#file").on("change",function(e) {
				console.log(e.currentTarget.files[0].path)
				$(".icon img").attr("src",e.currentTarget.files[0].path)
			});
		},
		changeHeader : function(){
			$("#file").click();
		},
	}
}
</script>

<style scoped>
	.mine-data{
		padding: 1rem;
	}
	.mine-data-list{
		margin-bottom: 1rem;
	}
	.mine-data-items{
		border-bottom: 1px solid #e5e5e5;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 1rem 1rem 1rem 0;
	}
	.items-value{
		color: #666;
	}
	.items-value select{
		font-size: 1rem;
		color: #666;
		outline: none;
		line-height:1rem;
		appearance:none;
		-webkit-appearance:none;
		border:none;
		background-color:white;
	}
	.items-value .icon{
		width: 1.562rem;
		height: 1.562rem;
		overflow: hidden;
		border-radius: 2rem;
	}
	.items-value .icon img{
		display: block;
	}
	.other-data-title{
		color: #999;
		margin-bottom: 1rem;
	}
</style>



