<template>
	<div class="login">
		<ul class="login-list">
			<li class="login-items">
				<input id="phone_num" type="text" placeholder="手机号码" maxlength="11">
				<div class="fail-tips hide">找不到该用户</div>
			</li>
			<li class="login-items login-password">
				<div class="login-items-left">
					<input id="password" class="password-input" type="password" placeholder="密码">
				</div>
				<div class="login-items-right hide" id="toggle_psw_btn" @click="togglePswFn()">{{togglePsw?"隐藏":"显示"}}</div>
			</li>
		</ul>
		<router-link tag="div" to="/Register/type/setting" class="forget-password">忘记密码？</router-link>
		<div class="login-btn" id="submit_btn" @click="loginFn">登录</div>
	</div>
</template>

<script>
export default {
	name: 'Login',
	data () {
		return {
			msg:"登录",
			togglePsw:false,
		}
	},
	created (){
		document.title="登录";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {
			var _this = this;
			$("input").on('input propertychange', function(e) {
				$(".fail-tips").hide();
				var isInput=0;
				$.each($("input"),function(i,o){
					if ($(o).val()!="") isInput++;
				})
				if (isInput==2) {
					$("#submit_btn").css({
						"background-color": '#000',
						"color": '#fff'
					}).on('click', function() {
						if ($("#phone_num").val().length!=11) {$(".fail-tips").show();return;};
						_this.$router.push({ path: '/' })
					})
				}else{
					$("#submit_btn").css({
						"background-color": '#EBEBEB',
						"color": '#999'
					})
				};
			});
			$("#password").on('input propertychange', function(e) {
				console.log($(e.target).val().length)
				if ($(e.target).val().length>0) {
					$("#toggle_psw_btn").show();
				}else{
					console.log($(e.target).val().length)
					$("#toggle_psw_btn").hide();
				}
			});
		},
		togglePswFn:function(){
			this.togglePsw=!this.togglePsw;
			if (this.togglePsw) {
				$("#password").attr("type","text");
			}else{
				$("#password").attr("type","password");
			}
		},
		loginFn:function(){
			// this.$router.push({path:'/'})
			$(".fail-tips").show();
		}
	}
}
</script>

<style scoped>
	.login{
		padding: 1rem;
	}
	.login-list{
		margin-top: 4rem;
	}
	.login-items{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;
	}
	.login-password{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.login-items input{
		width: 100%;
		font-size: 1rem;
	}
	.fail-tips{
		color: red;
		padding-top: 1rem;
	}
	.login-items-left{
		width: 90%;
	}
	.login-items-right{
		color: #999;
	}
	.forget-password{
		font-size: 0.875rem;
		padding: 1rem 0;
	}
	.login-btn{
		background-color: #EBEBEB;
		text-align: center;
		padding: 1rem;
		color: #999;
	}
</style>



