<template>
	<div class="register">
		<ul class="register-list">
			<li class="register-items">
				<input id="phone_num" type="text" placeholder="输入手机号码" maxlength="11">
			</li>
			<li class="register-items register-code">
				<div class="register-items-left">
					<input id="code" class="code-input" type="text" maxlength="4" placeholder="输入验证码">
				</div>
				<div class="register-items-right">
					<div class="code">
						<img src="../assets/code.png">
					</div>
					<div class="refresh" @click="refreshFn($event)"><img src="../assets/ico_refresh.png"></div>
				</div>
			</li>
		</ul>
		<div class="submit-button" id="submit_btn">确认</div>
	</div>
</template>

<script>
export default {
	name: 'Register',
	data () {
		return {
			msg:"注册",
			refreshBol:false
		}
	},
	created (){
		console.log(this.$route.params.type)
		if (this.$route.params.type=="register") {
			document.title="注册";
		}else if (this.$route.params.type=="setting") {
			document.title="忘记密码";
		}
		window.scrollTo(0, 0);
	},
	mounted () {
		if (this.$route.params.type=="register") {
			this.registerInit();
		}else if (this.$route.params.type=="setting") {
			this.settingInit();
		}
	},
	methods: {
		registerInit: function () {
			var _this = this;
			$("#submit_btn").click(function() {
				var telephone = $("#phone_num").val();
				// axios.post("/api/sms/send",{"type":1,"telephone":telephone}).then(res=>{
				// 	console.log(res)
				// 	console.log(res.data.response.data)
				// 	// if (res.data.code==200) {
				// 	// 	this.leagueMatchesList=res.data.data.items;
				// 	// 	$("#matches_endloading").hide();
				// 	// }
				// 	// _this.$router.push({ path: '/SettingPassword/type/register/tel/'+telephone })
				// })
				_this.$router.push({ path: '/SettingPassword/type/register/tel/'+telephone })
			});
		},
		settingInit: function () {
			var _this = this;
			$("#submit_btn").click(function() {
				var telephone = $("#phone_num").val();
				axios.post("/api/sms/send",{"type":2,"telephone":telephone}).then(res=>{
					console.log(res)
					console.log(res.data.response.data)
					// if (res.data.code==200) {
					// 	this.leagueMatchesList=res.data.data.items;
					// 	$("#matches_endloading").hide();
					// }
				})
				_this.$router.push({ path: '/SettingPassword/type/setting' })
			});
		},
		refreshFn:function(e){
			if (this.refreshBol) return;
			this.refreshBol==true;
			$(e.target).css({
				"-webkit-animation":"page-anim-loading 1s infinite linear",
				"animation": 'page-anim-loading 1s infinite linear',
			});
		},
	}
}
</script>

<style scoped>
	.register{
		padding: 1rem;
	}
	.register-list{
		margin-top: 4rem;
	}
	.register-items{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;
	}
	.register-code,.register-items-right{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		align-items: center;
		-webkit-align-items: center;
	}
	.register-items input{
		width: 100%;
		font-size: 1rem;
	}
	.register-items-left{
		width: 90%;
	}
	.register-items-right{
		color: #999;
	}
	.refresh{
		width: 1.2rem;
		padding: 0.5rem;
	}
	@keyframes page-anim-loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg);}}
</style>



